<template>
  <ion-page>
    <ion-header>
      <ion-toolbar>
        <ion-title>Quiz</ion-title>
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
        <div v-for="question in questions" v-bind:key="question">
            <p>{{question.q}}</p>
            <ion-input placeholder="Enter answer" v-model="question.a"></ion-input>

            <ion-button v-for="resource in question.resources" v-bind:key="resource" @click="showResource(resource)">Show resource</ion-button>
        </div>
        <ion-button expand="full" @click="submitAnswers()">Submit answers</ion-button>
    </ion-content>
  </ion-page>
</template>
<script>
import { modalController, toastController, IonContent, IonInput, IonButton, IonPage, IonToolbar, IonHeader, IonTitle } from '@ionic/vue';
import { warning } from 'ionicons/icons';
import { defineComponent } from 'vue';
import { useRouter } from 'vue-router';
import axios from 'axios';
import Resource from '@/components/modals/Resource.vue'

export default defineComponent({
  components: { IonInput, IonButton, IonContent, IonPage, IonToolbar, IonHeader, IonTitle },
  setup() {
    const router = useRouter();
    return { warning, router };
  },
  created: function(){
      this.id = this.id.substring(0, this.id.indexOf('?'));

      axios.get(process.env.VUE_APP_TEACHER_API_URL+'/quizmanagement/quiz/'+this.id+'?password='+this.$route.query.pass).then((response) => {
            response.data.questions.forEach(question => {
                const arr = question.resourceIds.split(';').map(Number);
                const index = arr.indexOf(0);
                if (index > -1) {
                    arr.splice(index, 1);
                }
                this.questions.push({
                    q: question.actualQuestion,
                    a: '',
                    id: question.id,
                    resources: arr
                });
            });
            this.name = response.data.name;
            this.description = response.data.description;
            this.password = response.data.password;
        }).catch(() => {
            this.failGetToast();
            this.router.push('/');
        });

  },
  methods: {
      async successSubmitToast() {
        const toast = await toastController
          .create({
            message: 'Operation succeeded.',
            duration: 2000
          })
        return toast.present();
      },
      async failedSubmitToast() {
        const toast = await toastController
          .create({
            message: 'Operation failed.',
            duration: 2000
          })
        return toast.present();
      },
      async failGetToast() {
        const toast = await toastController
          .create({
            message: 'Failed to get quiz. Password incorrect or quiz not initiated.',
            duration: 2000
          })
        return toast.present();
      },
      async showResource(id){
          const modal = await modalController
          .create({
            component: Resource,
            componentProps: {
              id: id
            },
          })
        return modal.present();
      },
      submitAnswers(){
          const parseJwt = (token) => {
                const base64Url = token.split('.')[1];
                const base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
                const jsonPayload = decodeURIComponent(atob(base64).split('').map(function(c) {
                    return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
                }).join(''));

                return JSON.parse(jsonPayload);
            };
          
          const username = parseJwt(window.localStorage.getItem('loggedInJwt'))?.sub;
          const password = this.$route.query.pass;
          const answers = [];
          this.questions.forEach((el) => {
              answers.push({
                  values: el.a,
                  questionId: el.id
              })
          });

          const payload = {
              username: username,
              password: password,
              answers: answers
          };
        
          axios.post(process.env.VUE_APP_TEACHER_API_URL+'/quizmanagement/quiz/'+this.id+'/answer', payload).then(() => {
                    this.successSubmitToast();
                    this.router.push("/tabs/quizzes");
                }).catch(() => {
                    this.failedSubmitToast();
                });
      }
  },
  data: () => ({
      questions: [],
      id: window.location.href.substring(window.location.href.lastIndexOf('/')+1,window.location.href.length),
  }),
});
</script>

