<template>
<ion-page>
    <ion-header>
      <ion-toolbar>
        <ion-title>Resources</ion-title><br>
        <ion-button @click="closeModal()">Close</ion-button>
      </ion-toolbar>
    </ion-header>
  <ion-content :fullscreen="true"
    :scroll-events="false">
    <div v-if="!showViewer">
      <h1>{{title}}</h1>
      <ion-list>
        <ion-item v-for="file in files" :key="file">
            <ion-checkbox v-if="file.fileName.endsWith('.glb') || file.fileName.endsWith('.gltf')" @update:modelValue="handleClick($event, file.id)"
            :modelValue="fileIds.indexOf(file.id) != -1"></ion-checkbox>
            <ion-label class="checkbox_label" v-if="file.fileName.endsWith('.glb') || file.fileName.endsWith('.gltf')">{{file.fileName}}</ion-label>
            <span v-else>
              {{file.fileName}}  
            </span>
            <ion-button v-if="file.fileName.endsWith('.glb')" fill="clear" @click="viewIn3d(file.id)">
            <ion-icon :icon="eye" /></ion-button>
        </ion-item>
      </ion-list>
      <ion-button v-if="fileIds.length != 0" :href="viewFile()" expand="full" >View checked models in AR</ion-button>
    </div>
    <div v-else>
      <model-viewer v-if="showViewer" :src="modelSrc" camera-controls style="width:100%;height: 500px"></model-viewer>
      <ion-button v-if="showViewer" expand="full" @click="closeViewer" >Close</ion-button>
    </div>
  </ion-content>
</ion-page>
</template>

<script>
import { modalController, IonItem, IonList, IonLabel, IonContent, IonCheckbox, IonButton, IonIcon, IonTitle, IonToolbar, IonHeader, IonPage } from '@ionic/vue';
import { eye } from 'ionicons/icons';
import { defineComponent } from 'vue';
import ModelViewer from '@google/model-viewer';
import axios from 'axios';

export default defineComponent({
  components: { IonItem, IonList, IonLabel, IonContent, IonCheckbox, IonButton, IonIcon, ModelViewer, IonTitle, IonToolbar, IonHeader, IonPage },
  setup() {
    return { eye };
  },
  methods: {
      viewIn3d(fileId){
        this.modelSrc = process.env.VUE_APP_API_URL+'/resourceEntries/'+this.resourceId+'/files/'+fileId;
        this.showViewer = true;
      },
      closeViewer(){
        this.modelSrc = '';
        this.showViewer = false;
      },
      async closeModal(){
          await modalController.dismiss()
      },
      handleClick(isAdd, id){
        if (!isAdd){
          const index = this.fileIds.indexOf(id);
          if (index != -1){
            this.fileIds.splice(index, 1);
          }
        }else{
          this.fileIds.push(id);
        }
      },
      viewFile() {
        return '/ar.html?resourceId='+this.resourceId+'&fileIds='+this.fileIds.join(',')+"&backUrl="+process.env.VUE_APP_BACKEND;
      }
  },
  data: () => ({
    title: "",
    files: [].data,
    fileIds: [],
    resourceId: null,
    showViewer: false,
    modelSrc: ''
  }),
  props: {
    id: Number,
  },
  created: function(){
    axios.get(process.env.VUE_APP_API_URL+'/resourceEntries/'+this.id).then((response) => {
      this.title = response.data.title;
      this.files = response.data.files;
      this.resourceId = this.id;
    });
  },
});
</script>

<style scoped>
.preview-image {
  display: block;
  margin-left: auto;
  margin-right: auto;
  height: 250px;
}
.checkbox_label {
  margin-left: 1%;
}
</style>